'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { useFindMany, useDeleteOne, useUpdateOne } from '@/hooks/useDynamicGraphQL';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { 
  Plus, 
  Search, 
  Edit, 
  Trash2, 
  Eye,
  Users,
  Clock,
  Star,
  AlertCircle,
  BookOpen,
  Sparkles,
} from 'lucide-react';
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useToast } from '@/hooks/use-toast';

interface Course {
  id: string;
  title: string;
  slug: string;
  description: string;
  level: string;
  price: number;
  duration: number;
  thumbnailUrl: string;
  status: string;
  instructor: {
    id: string;
    firstName: string;
    lastName: string;
    username: string;
  };
  _count: {
    enrollments: number;
    modules: number;
    reviews: number;
  };
  createdAt: string;
}

export default function AdminCoursesPage() {
  const router = useRouter();
  const { toast } = useToast();
  const [searchQuery, setSearchQuery] = useState('');
  const [filterStatus, setFilterStatus] = useState<'all' | 'published' | 'draft'>('all');
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [courseToDelete, setCourseToDelete] = useState<any>(null);

  const { data: courses, loading, error, refetch } = useFindMany('Course', {
    select: {
      id: true,
      title: true,
      slug: true,
      description: true,
      level: true,
      price: true,
      thumbnail: true,
      status: true,
      createdAt: true,
      duration: true,
    },
    include: {
      _count: {
        select: {
          enrollments: true,
          modules: true,
          reviews: true,
        },
      },
      instructor: {
        select: {
          id: true,
          firstName: true,
          lastName: true,
          username: true,
        },
      },
    },
    orderBy: {
      createdAt: 'desc',
    },
  });

  const [deleteCourse, { loading: deleteLoading }] = useDeleteOne('Course');
  const [updateCourse] = useUpdateOne('Course');

  // Filter courses
  const filteredCourses = (courses || []).filter(course => {
    const matchesSearch = course.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         (course.description || '').toLowerCase().includes(searchQuery.toLowerCase());
    const matchesFilter = filterStatus === 'all' || 
                         (filterStatus === 'published' && course.status === 'PUBLISHED') ||
                         (filterStatus === 'draft' && course.status === 'DRAFT');
    return matchesSearch && matchesFilter;
  });

  // Handlers
  const handleCreateCourse = () => {
    router.push('/lms/admin/courses/create');
  };

  const handleCreateWithAI = () => {
    router.push('/lms/admin/courses/create-with-ai');
  };

  const handleViewCourse = (courseId: string) => {
    router.push(`/lms/admin/courses/${courseId}`);
  };

  const handleEditCourse = (courseId: string) => {
    router.push(`/lms/admin/courses/${courseId}/edit`);
  };

  const handleDeleteClick = (course: any) => {
    setCourseToDelete(course);
    setDeleteDialogOpen(true);
  };

  const handleDeleteConfirm = async () => {
    if (!courseToDelete) return;

    try {
      await deleteCourse({
        where: { id: courseToDelete.id },
      });

      toast({
        title: 'Th√†nh c√¥ng',
        description: `ƒê√£ x√≥a kh√≥a h·ªçc "${courseToDelete.title}"`,
        type: 'success',
      });

      setDeleteDialogOpen(false);
      setCourseToDelete(null);
      refetch();
    } catch (error: any) {
      toast({
        title: 'L·ªói',
        description: error.message || 'Kh√¥ng th·ªÉ x√≥a kh√≥a h·ªçc',
        type: 'error',
      });
    }
  };

  const handleTogglePublish = async (course: any) => {
    try {
      const newStatus = course.status === 'PUBLISHED' ? 'DRAFT' : 'PUBLISHED';
      await updateCourse({
        where: { id: course.id },
        data: {
          status: newStatus,
        },
      });

      toast({
        title: 'Th√†nh c√¥ng',
        description: `ƒê√£ ${newStatus === 'PUBLISHED' ? 'xu·∫•t b·∫£n' : 'chuy·ªÉn v·ªÅ nh√°p'} kh√≥a h·ªçc "${course.title}"`,
        type: 'success',
      });

      refetch();
    } catch (error: any) {
      toast({
        title: 'L·ªói',
        description: error.message || 'Kh√¥ng th·ªÉ c·∫≠p nh·∫≠t tr·∫°ng th√°i',
        type: 'error',
      });
    }
  };

  // Format price
  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND',
    }).format(price);
  };

  return (
    <div className="p-8 space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between flex-wrap gap-4">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Qu·∫£n l√Ω kh√≥a h·ªçc</h1>
          <p className="text-gray-600 mt-1">T·ªïng c·ªông {courses?.length || 0} kh√≥a h·ªçc</p>
        </div>
        <div className="flex gap-2">
          <Button onClick={handleCreateCourse} variant="outline" className="gap-2">
            <Plus className="w-4 h-4" />
            T·∫°o th·ªß c√¥ng
          </Button>
          <Button 
            onClick={handleCreateWithAI} 
            className="gap-2 bg-gradient-to-r from-purple-600 to-blue-600 hover:from-purple-700 hover:to-blue-700"
          >
            <Sparkles className="w-4 h-4" />
            T·∫°o v·ªõi AI
          </Button>
        </div>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4">
        <div className="flex-1 relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
          <Input
            placeholder="T√¨m ki·∫øm kh√≥a h·ªçc..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10"
          />
        </div>
        <div className="flex gap-2">
          <Button
            variant={filterStatus === 'all' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('all')}
          >
            T·∫•t c·∫£
          </Button>
          <Button
            variant={filterStatus === 'published' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('published')}
          >
            ƒê√£ xu·∫•t b·∫£n
          </Button>
          <Button
            variant={filterStatus === 'draft' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('draft')}
          >
            Nh√°p
          </Button>
        </div>
      </div>

      {/* Courses Grid */}
      {loading ? (
        <div className="text-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="text-gray-500 mt-4">ƒêang t·∫£i...</p>
        </div>
      ) : error ? (
        <Card>
          <CardContent className="py-12 text-center">
            <AlertCircle className="w-12 h-12 text-red-500 mx-auto mb-4" />
            <p className="text-red-600">L·ªói: {error.message}</p>
          </CardContent>
        </Card>
      ) : filteredCourses.length === 0 ? (
        <Card>
          <CardContent className="py-12 text-center">
            <BookOpen className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-500">Kh√¥ng t√¨m th·∫•y kh√≥a h·ªçc n√†o</p>
            <Button onClick={handleCreateCourse} className="mt-4">
              T·∫°o kh√≥a h·ªçc ƒë·∫ßu ti√™n
            </Button>
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
          {filteredCourses.map((course) => (
            <Card key={course.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <div className="flex items-start justify-between mb-2">
                  <Badge 
                    variant={course.status === 'PUBLISHED' ? 'default' : 'secondary'}
                    className="cursor-pointer"
                    onClick={() => handleTogglePublish(course)}
                  >
                    {course.status === 'PUBLISHED' ? 'ƒê√£ xu·∫•t b·∫£n' : 'Nh√°p'}
                  </Badge>
                  <Badge variant="outline">
                    {course.level || 'Beginner'}
                  </Badge>
                </div>
                <CardTitle className="line-clamp-2">{course.title}</CardTitle>
                <CardDescription className="mt-2 line-clamp-2">
                  {course.description || 'Ch∆∞a c√≥ m√¥ t·∫£'}
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Instructor */}
                {course.instructor && (
                  <div className="flex items-center gap-2 text-sm text-gray-600">
                    <Users className="w-4 h-4" />
                    <span>Gi·∫£ng vi√™n: {course.instructor.firstName && course.instructor.lastName 
                      ? `${course.instructor.firstName} ${course.instructor.lastName}` 
                      : course.instructor.username}</span>
                  </div>
                )}

                {/* Stats */}
                <div className="flex items-center gap-4 text-sm text-gray-600">
                  <div className="flex items-center gap-1">
                    <Users className="w-4 h-4" />
                    <span>{course._count?.enrollments || 0}</span>
                  </div>
                  <div className="flex items-center gap-1">
                    <BookOpen className="w-4 h-4" />
                    <span>{course._count?.modules || 0} modules</span>
                  </div>
                  <div className="flex items-center gap-1">
                    <Clock className="w-4 h-4" />
                    <span>{course.duration || 0}p</span>
                  </div>
                  <div className="flex items-center gap-1">
                    <Star className="w-4 h-4 text-yellow-500" />
                    <span>{course._count?.reviews || 0}</span>
                  </div>
                </div>

                {/* Price */}
                <div className="text-lg font-bold text-blue-600">
                  {course.price ? formatPrice(course.price) : 'Mi·ªÖn ph√≠'}
                </div>

                {/* Actions */}
                <div className="flex gap-2 pt-2">
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="flex-1 gap-2"
                    onClick={() => handleViewCourse(course.id)}
                  >
                    <Eye className="w-4 h-4" />
                    Xem
                  </Button>
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="flex-1 gap-2"
                    onClick={() => handleEditCourse(course.id)}
                  >
                    <Edit className="w-4 h-4" />
                    S·ª≠a
                  </Button>
                  <Button 
                    variant="outline" 
                    size="sm" 
                    className="gap-2 text-red-600 hover:text-red-700 hover:bg-red-50"
                    onClick={() => handleDeleteClick(course)}
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>X√°c nh·∫≠n x√≥a kh√≥a h·ªçc</AlertDialogTitle>
            <AlertDialogDescription>
              B·∫°n c√≥ ch·∫Øc ch·∫Øn mu·ªën x√≥a kh√≥a h·ªçc <strong>&quot;{courseToDelete?.title}&quot;</strong>?
              <br />
              <br />
              <span className="text-red-600 font-semibold">
                H√†nh ƒë·ªông n√†y kh√¥ng th·ªÉ ho√†n t√°c v√† s·∫Ω x√≥a t·∫•t c·∫£ d·ªØ li·ªáu li√™n quan (b√†i h·ªçc, quiz, enrollment, v.v.)
              </span>
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>H·ªßy</AlertDialogCancel>
            <AlertDialogAction
              onClick={handleDeleteConfirm}
              disabled={deleteLoading}
              className="bg-red-600 hover:bg-red-700"
            >
              {deleteLoading ? 'ƒêang x√≥a...' : 'X√≥a kh√≥a h·ªçc'}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </div>
  );
}
